.contact {
    display: flex;
    flex-direction: column;
    background-color: white;
    padding-bottom: 140px;
    font-size: 25px;
    color: #E50D6D;
    padding: 60px 0 60px 60px;
    @media(max-width: 600px) {
        padding: 20px;
    }
    &__item {
        display: flex;
        flex-direction: row;
        align-items: center;
        @media(max-width: 600px) {
            width: 95%;
            }
        &--icon {
            font-size: 40px;
            margin: 20px;
        }
        &--text {
            margin: 0;
            @media(max-width: 600px) {
                width: 95%;
                font-size: 16px;
                overflow:scroll;
                }
        }
    }
}

.form {
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items: center;
    &__group {
        width: 50%;
        display: flex;
        flex-direction: column;
        margin-top: 40px;
        @media(max-width: 600px) {
            width: 95%;
            }
        &--title {
            font-weight: 500;
            font-size: 1.125em;
        }
        &--content {
            min-height: 30px;
            border: none;
            box-shadow: 0 5px 7px 3px rgba(189, 189, 192, 0.5), -3px -3px 2px 0 rgba(207, 207, 207, 0.075);
        }
        &--message {
            height: 160px;
        }
    }
}

.form__button {
    background-color:  rgba(0, 48, 99, 1);
    border: none;
    color: white;
    width: fit-content;
    padding: 10px 20px;
    border-radius: 25px;
    margin-top: 40px;
}